.wrapper {
  display: flex;
  flex-direction: column;

  color: #fff;
  text-align: center;
  height: calc(100vh - 50px);
  margin: 10px;
  padding: 15px;
  box-shadow: 0 0 3px 3px #fff;
  border-radius: 10px;
  /*background: #eee;*/
}

.container {
  display: flex;
  flex: 1;
}
.container.row {
  flex-direction: row;

}
.container.row.info {
  align-items: flex-start;
  flex: 0.75 1;
}
.container.row.top {
  align-items: flex-start;
  flex: 12;
}
.container.row.middle {
  /*align-items: center;*/
  flex: 8;
}
.container.row.bottom {
  align-items: flex-end;
  flex: 4;
}

.container.row .col {
  flex: 1 1 auto;
}

.c1 { background: rgba(255,0,0,0.2); }
.c2 { background: rgba(0,255,0,0.2); }
.c3 { background: rgba(255,255,0,0.2); }
.c4 { background: rgba(255,0,255,0.2); }
.c5 { background: rgba(0,0,255,0.2); }
.c6 { background: rgba(0,255,255,0.2); }
.c7 { background: rgba(255,120,0,0.2); }
.c8 { background: rgba(255,80,120,0.2); }
.c9 { background: rgba(255,200,50,0.2); }




.container.row.info p {
  margin: 4px 0;
  font-size: 0.8em;
}

